.greetingForm {
    margin-bottom: 50px;
}

.inputAndButtonContainer {
}

.error {
color: red;
}

.input {
    background: #F5F7FB;
    border: 1px solid #D1D1D1;
    border-radius: 5px;
    width: 372px;
    min-height: 36px;
    padding: 10px;
}

.input:focus {
}

.errorInput {
    border: 3px solid red;
    background: #F5F7FB;
    border-radius: 5px;
    width: 372px;
    min-height: 36px;
    padding: 10px;
}

.button {
    background: #0066CC;
    border-radius: 3px;
    color: #FFFFFF;
    font-size: 14px;
    width: 78px;
    height: 36px;
    margin-left: 10px;
    transition: transform .2s;
    cursor: pointer;
}

.button:focus {
}

.button:active {
}

.button:disabled {
    background: #0066CC;
    opacity: 0.5;
    border-radius: 3px;
    color: #FFFFFF;
    font-size: 14px;
    width: 78px;
    height: 36px;
    margin-left: 10px;
    transition: transform .2s;
    cursor: pointer;
}

.text {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: rgba(0, 0, 0, 0.5);
    margin-bottom: 10px;
}

.greeting {
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    margin-top: 10px;
}
