.button {
    color: #FFF;
    width: 132px;
    height: 30px;
    border-radius: 3px;
}

.disabled {
    background: #004D99;
    opacity: 0.5;
    color:  #002E5C;
}

.secondary {
    width: 176px;
    border: 1px solid #0066CC;
    color: #0066CC;
}

.default {
    background: #0066CC;


}

.red {
    background: #CC1439;
}

.default:hover {
   background: #0080FF;
    width: 167px;
}

.red:hover {
    background: #FF1A47;
    width: 167px;
}

.secondary:hover {
    background: #E5F0FA;
    width: 167px;
}


.default:active {
    background: #0059B3;
    width: 149px;
}

.secondary:active {
    background: #C9E5FF;
    width: 149px;
}

.red:active {
    background: #B31232;
    width: 149px;
}

.button:disabled {
}
